import type { NextPage, GetStaticPropsContext } from "next";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import { useTranslations } from "next-intl";

import ResponsiveAppBar from "../src/components/ResponsiveAppBar";
import React from "react";
import Head from "next/head";
import Paper from "@mui/material/Paper";

import { useTheme } from "../src/theme";
import SearchForm from "../src/components/SearchForm";
import { Footer } from "../src/components/Footer";
import Link from "../src/components/Link";

import { Typography } from "@mui/material";
import { useRouter } from "next/router";

const Countries: NextPage = ({ countries }: any) => {
  const theme = useTheme();
  const t = useTranslations();
  const { locale } = useRouter();
  return (
    <Box
      sx={{
        backgroundColor: theme.palette.mode === "light" ? "#F3F6F9" : "inherit",
      }}
    >
      <Head>
        <title>
          {t("countries")} - {t("cheap-flights-and-airline-deals")} |
          Flightscanner
        </title>
      </Head>
      <ResponsiveAppBar />
      <Paper
        variant={theme.palette.mode === "light" ? "outlined" : "elevation"}
        sx={{ pb: 2, borderLeft: "none", borderRight: "none" }}
        square
      >
        <Typography
          variant="h3"
          gutterBottom
          sx={{ textAlign: "center", pt: 4 }}
        >
          {t("countries-page-countries")}
        </Typography>
        <SearchForm maxWidth="lg" showBooking={false} />
      </Paper>

      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Paper sx={{ p: 2 }}>
          <Box component="ul" sx={{ display: "flex", flexWrap: "wrap", listStyle: "none", p: 0 }}>
            {countries.map((country: any) => {
              const slug =
                country.slug_translations[locale as string] ||
                country.slug_translations["en"];
              const name =
                country.name_translations[locale as string] ||
                country.name_translations["en"];
              return (
                <Box
                  key={name}
                  component="li"
                  sx={{ flex: { xs: "100%", sm: "50%", md: "33%" } }}
                >
                  <Link
                    sx={{ display: "block" }}
                    href={`/${t("routes-country")}/${slug}`}
                    underline="hover"
                    color="inherit"
                  >
                    {name}
                  </Link>
                </Box>
              );
            })}
          </Box>
        </Paper>
      </Container>
      <Footer />
    </Box>
  );
};

export async function getStaticProps({ locale }: GetStaticPropsContext) {
  const countries = require("../data/countries-with-slug.json");

  return {
    props: {
      countries,
      // You can get the messages from anywhere you like. The recommended
      // pattern is to put them in JSON files separated by language and read
      // the desired one based on the `locale` received from Next.js.
      messages: (await import(`../messages/index/${locale || 'en'}.json`)).default,
    },
  };
}

export default Countries;
